"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { StatsCard } from "@/components/ui/StatsCard";
import { RevenueChart } from "@/components/charts/RevenueChart";
import { OrderStatusChart } from "@/components/charts/OrderStatusChart";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
    DollarSign,
    ShoppingCart,
    Users,
    TrendingUp,
    Package,
    Clock,
    Plus,
    CreditCard,
    ArrowRight,
    Search,
    RefreshCw
} from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { format } from "date-fns";
import Link from "next/link";
import { toast } from "sonner";
import { cn } from "@/lib/utils";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { DateRange } from "react-day-picker";


interface Order {
    id: number;
    reference: string;
    cost: number;
    status: string;
    created_at: string;
    user?: { name: string; email: string };
    bundle?: { name: string };
}

interface AnalyticsStats {
    total_revenue: number;
    total_profit: number;
    total_orders: number;
    total_users: number;
    new_users: number;
    pending_deposits: number;
    pending_orders: number;

    recent_orders: Order[];
    monthly_revenue: { month: string; total: number }[];
    order_status: { status: string; count: number }[];
}

export default function AdminDashboardPage() {
    const [stats, setStats] = useState<AnalyticsStats | null>(null);
    const [loading, setLoading] = useState(true);
    const [refreshing, setRefreshing] = useState(false);
    const [dateRange, setDateRange] = useState<DateRange | undefined>({
        from: new Date(new Date().setDate(new Date().getDate() - 30)),
        to: new Date()
    });


    const loadData = async (range?: DateRange) => {
        setRefreshing(true);
        try {
            const params: any = {};
            if (range?.from) params.start_date = format(range.from, "yyyy-MM-dd");
            if (range?.to) params.end_date = format(range.to, "yyyy-MM-dd");

            const res = await api.get('/admin/analytics', { params });
            setStats(res.data);
        } catch (err) {
            console.error("Failed to fetch analytics", err);
            toast.error("Failed to refresh dashboard data");
        } finally {
            setLoading(false);
            setRefreshing(false);
        }
    };

    useEffect(() => {
        loadData(dateRange);
    }, [dateRange]);


    const getStatusColor = (status: string) => {
        switch (status.toLowerCase()) {
            case "completed": return "bg-green-100 text-green-700 border-green-200 dark:bg-green-900/30 dark:text-green-400";
            case "processing": return "bg-blue-100 text-blue-700 border-blue-200 dark:bg-blue-900/30 dark:text-blue-400";
            case "pending": return "bg-yellow-100 text-yellow-700 border-yellow-200 dark:bg-yellow-900/30 dark:text-yellow-400";
            case "failed": return "bg-red-100 text-red-700 border-red-200 dark:bg-red-900/30 dark:text-red-400";
            default: return "bg-gray-100 text-gray-700 border-gray-200 dark:bg-gray-800 dark:text-gray-400";
        }
    };

    if (loading) {
        return (
            <div className="space-y-8 animate-pulse">
                <div className="h-10 w-48 bg-gray-200 dark:bg-gray-800 rounded-lg"></div>
                <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-4">
                    {[...Array(4)].map((_, i) => (
                        <div key={i} className="h-32 bg-gray-200 dark:bg-gray-800 rounded-2xl"></div>
                    ))}
                </div>
                <div className="grid gap-6 lg:grid-cols-2">
                    <div className="h-[400px] bg-gray-100 dark:bg-gray-900 rounded-2xl"></div>
                    <div className="h-[400px] bg-gray-100 dark:bg-gray-900 rounded-2xl"></div>
                </div>
            </div>
        );
    }

    if (!stats) return <div className="text-center py-20 text-red-500 font-bold">Failed to load analytics. Please try again later.</div>;

    // Transform data for charts
    const revenueChartData = stats.monthly_revenue.map(m => ({
        name: format(new Date(m.month + "-01"), "MMM"),
        value: Number(m.total)
    }));

    const statusChartData = stats.order_status.map(s => ({
        name: s.status.charAt(0).toUpperCase() + s.status.slice(1),
        value: s.count
    }));

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="space-y-8 pb-10"
        >
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                <div>
                    <h2 className="text-3xl font-black tracking-tight text-slate-900 dark:text-white">Grand Overview</h2>
                    <p className="text-muted-foreground font-medium">Real-time business performance and system health.</p>
                </div>
                <div className="flex flex-wrap items-center gap-2">
                    <Popover>
                        <PopoverTrigger asChild>
                            <Button variant="outline" className="h-11 rounded-2xl border-slate-200 dark:border-slate-800 font-bold px-4">
                                <TrendingUp className="w-4 h-4 mr-2" />
                                {dateRange?.from ? (
                                    dateRange.to ? (
                                        <>{format(dateRange.from, "LLL dd")} - {format(dateRange.to, "LLL dd")}</>
                                    ) : (format(dateRange.from, "LLL dd"))
                                ) : (<span>Select Range</span>)}
                            </Button>
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0" align="end">
                            <Calendar
                                initialFocus
                                mode="range"
                                selected={dateRange}
                                onSelect={setDateRange}
                                numberOfMonths={2}
                            />
                        </PopoverContent>
                    </Popover>
                    <Button
                        variant="outline"
                        size="sm"
                        onClick={() => loadData(dateRange)}
                        disabled={refreshing}
                        className="h-11 rounded-2xl border-gray-200 dark:border-gray-800 font-bold px-4"
                    >
                        <RefreshCw className={cn("w-4 h-4 mr-2", refreshing && "animate-spin")} />
                        Refresh
                    </Button>
                    <Button asChild size="sm" className="h-11 rounded-2xl shadow-lg shadow-primary/20 font-bold px-6">
                        <Link href="/admin/products">
                            <Plus className="w-4 h-4 mr-2" />
                            New Product
                        </Link>
                    </Button>
                </div>

            </div>

            {/* Main Stats Grid */}
            <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-4">
                <StatsCard
                    title="Revenue"
                    value={`${Number(stats.total_revenue).toLocaleString(undefined, { minimumFractionDigits: 2 })}`}
                    changeLabel="Selected Period"
                    icon={DollarSign}
                    iconColor="text-indigo-600"
                    iconBgColor="bg-indigo-100 dark:bg-indigo-900/30"
                />
                <StatsCard
                    title="Est. Profit"
                    value={`${Number(stats.total_profit).toLocaleString(undefined, { minimumFractionDigits: 2 })}`}
                    changeLabel="Net Over Costs"
                    icon={TrendingUp}
                    iconColor="text-emerald-600"
                    iconBgColor="bg-emerald-100 dark:bg-emerald-900/30"
                />
                <StatsCard
                    title="Orders Fulfilled"
                    value={stats.total_orders}
                    changeLabel="In range"
                    icon={ShoppingCart}
                    iconColor="text-blue-600"
                    iconBgColor="bg-blue-100 dark:bg-blue-900/30"
                />
                <StatsCard
                    title="User Growth"
                    value={stats.total_users}
                    change={stats.new_users}
                    changeLabel="New in period"
                    icon={Users}
                    iconColor="text-violet-600"
                    iconBgColor="bg-violet-100 dark:bg-violet-900/30"
                />
            </div>


            <div className="grid gap-6 lg:grid-cols-3">
                {/* Revenue Chart */}
                <div className="lg:col-span-2">
                    <RevenueChart data={revenueChartData} title="Revenue Growth Trends" />
                </div>

                {/* Quick Actions & Status */}
                <div className="space-y-6">
                    <Card className="sneat-card border-none shadow-sm bg-gradient-to-br from-primary to-indigo-700 text-white overflow-hidden relative min-h-[200px] flex flex-col justify-center">
                        <div className="absolute top-0 right-0 w-32 h-32 bg-white/10 rounded-full -mr-16 -mt-16 blur-2xl"></div>
                        <CardHeader className="pb-2">
                            <CardTitle className="text-sm font-bold uppercase tracking-widest opacity-80">Quick Actions</CardTitle>
                        </CardHeader>
                        <CardContent className="space-y-3 relative z-10">
                            <Button asChild variant="secondary" className="w-full justify-between rounded-xl bg-white/20 hover:bg-white/30 border-none text-white transition-all hover:translate-x-1">
                                <Link href="/admin/deposits">
                                    <span className="flex items-center gap-2 font-bold"><CreditCard className="w-4 h-4" /> Manage Deposits</span>
                                    {stats.pending_deposits > 0 && <Badge className="bg-orange-500 border-none">{stats.pending_deposits}</Badge>}
                                </Link>
                            </Button>
                            <Button asChild variant="secondary" className="w-full justify-between rounded-xl bg-white/20 hover:bg-white/30 border-none text-white transition-all hover:translate-x-1">
                                <Link href="/admin/orders?status=pending">
                                    <span className="flex items-center gap-2 font-bold"><Package className="w-4 h-4" /> Process Orders</span>
                                    {stats.pending_orders > 0 && <Badge className="bg-yellow-500 border-none text-black">{stats.pending_orders}</Badge>}
                                </Link>
                            </Button>
                            <Button asChild variant="secondary" className="w-full justify-between rounded-xl bg-white/20 hover:bg-white/30 border-none text-white transition-all hover:translate-x-1">
                                <Link href="/admin/users">
                                    <span className="flex items-center gap-2 font-bold"><Users className="w-4 h-4" /> Manage Users</span>
                                    <ArrowRight className="w-4 h-4" />
                                </Link>
                            </Button>
                        </CardContent>
                    </Card>

                    <OrderStatusChart data={statusChartData} />
                </div>
            </div>

            {/* Recent Orders Table */}
            <Card className="sneat-card border-none shadow-sm overflow-hidden">
                <CardHeader className="flex flex-row items-center justify-between border-b border-gray-50 dark:border-gray-800 bg-white/50 dark:bg-gray-900/50 backdrop-blur-md">
                    <div>
                        <CardTitle className="text-lg flex items-center gap-2">
                            <Clock className="w-5 h-5 text-primary" />
                            Latest Transactions
                        </CardTitle>
                        <CardDescription>Real-time stream of incoming orders.</CardDescription>
                    </div>
                    <Button asChild variant="ghost" size="sm" className="rounded-xl text-primary hover:bg-primary/5">
                        <Link href="/admin/orders">View All Orders <ArrowRight className="w-4 h-4 ml-1" /></Link>
                    </Button>
                </CardHeader>
                <CardContent className="p-0">
                    <div className="overflow-x-auto">
                        <table className="w-full text-sm text-left">
                            <thead className="text-[10px] text-slate-500 uppercase bg-slate-50/50 dark:bg-slate-900/50 font-bold tracking-wider">
                                <tr>
                                    <th className="px-6 py-4">Status</th>
                                    <th className="px-6 py-4">Reference</th>
                                    <th className="px-6 py-4">Customer</th>
                                    <th className="px-6 py-4">Bundle</th>
                                    <th className="px-6 py-4">Cost</th>
                                    <th className="px-6 py-4">Date</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-slate-100 dark:divide-slate-800">
                                <AnimatePresence mode="popLayout">
                                    {stats.recent_orders.length === 0 ? (
                                        <tr>
                                            <td colSpan={6} className="px-6 py-20 text-center text-muted-foreground italic">
                                                No orders available at the moment.
                                            </td>
                                        </tr>
                                    ) : (
                                        stats.recent_orders.map((order, index) => (
                                            <motion.tr
                                                key={order.id}
                                                initial={{ opacity: 0, x: -10 }}
                                                animate={{ opacity: 1, x: 0 }}
                                                transition={{ delay: index * 0.05 }}
                                                className="hover:bg-slate-50/80 dark:hover:bg-slate-800/80 transition-all group"
                                            >
                                                <td className="px-6 py-4">
                                                    <Badge variant="outline" className={cn("rounded-full px-3 py-0.5 border shadow-none", getStatusColor(order.status))}>
                                                        {order.status}
                                                    </Badge>
                                                </td>
                                                <td className="px-6 py-4">
                                                    <span className="font-mono text-xs font-bold text-slate-400 group-hover:text-primary transition-colors">#{order.reference}</span>
                                                </td>
                                                <td className="px-6 py-4">
                                                    <div className="flex flex-col">
                                                        <span className="font-bold text-slate-900 dark:text-slate-100">{order.user?.name || 'Customer'}</span>
                                                        <span className="text-[10px] text-slate-500">{order.user?.email}</span>
                                                    </div>
                                                </td>
                                                <td className="px-6 py-4">
                                                    <span className="font-medium text-slate-700 dark:text-slate-300">{order.bundle?.name || 'Data Package'}</span>
                                                </td>
                                                <td className="px-6 py-4 font-black text-slate-900 dark:text-white">
                                                    GHS {Number(order.cost).toFixed(2)}
                                                </td>
                                                <td className="px-6 py-4 text-slate-500 text-xs font-medium">
                                                    {format(new Date(order.created_at), "MMM dd, HH:mm")}
                                                </td>
                                            </motion.tr>
                                        ))
                                    )}
                                </AnimatePresence>
                            </tbody>
                        </table>
                    </div>
                </CardContent>
            </Card>
        </motion.div>
    );
}
